.avatar {
  position: relative;
  border-radius: 100%;
  position: relative;
  background-color: var(--primary-50);
  color: var(--primary-500);

  text-align: center;
  font-weight: 600;
  font-size: 4vw;
}

.avatar img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
  border-radius: 100%;
}

.avatar .mask {
  width: 100%;
  height: 100%;
  border-radius: 100%;
  overflow: hidden;
  position: relative;
}
.avatar svg {
  position: absolute;
  bottom: 0px;
  left: 0px;
  width: 100%;
  height: 80%;
}

.avatar.xs {
  width: 24px;
  height: 24px;
  font-size: 11px;
  line-height: 24px;
}

.avatar.sm {
  width: 32px;
  height: 32px;
  font-size: 12px;
  line-height: 30px;
}

.avatar.md {
  width: 40px;
  height: 40px;
  font-size: 16px;
  line-height: 39px;
}

.avatar.lg {
  width: 48px;
  height: 48px;
  font-size: 25px;
  line-height: 45px;
}

.avatar.xl {
  width: 64px;
  height: 64px;
  font-size: 32px;
  line-height: 60px;
}

.avatar.xxl {
  width: 96px;
  height: 96px;
  font-size: 48px;
  line-height: 90px;
}

.avatar.xxxl {
  width: 128px;
  height: 128px;
  font-size: 64px;
  line-height: 120px;
}

.avatar-active:after {
  content: '';
  background: #4caf50;
  border: 2px solid #ffffff;
  width: 16px;
  height: 16px;
  position: absolute;
  border-radius: 100%;
  bottom: 0px;
  right: 0px;
}

.avatar.sm.avatar-active:after {
  width: 8px;
  height: 8px;
}

.avatar-active.sm:after {
  width: 8px;
  height: 8px;
}

.avatar.xs.avatar-active:after {
  width: 6px;
  height: 6px;
}

.avatar-active.xs:after {
  width: 6px;
  height: 6px;
}

.avatar.md.avatar-active:after {
  width: 10px;
  height: 10px;
}

.avatar-active.md:after {
  width: 10px;
  height: 10px;
}

.avatar.lg.avatar-active:after {
  width: 12px;
  height: 12px;
}

.avatar.avatar-active.lg:after {
  width: 12px;
  height: 12px;
}

.avatar.xxl.avatar-active:after {
  width: 24px;
  height: 24px;
}

.avatar-active.xxl:after {
  width: 24px;
  height: 24px;
}

.avatar.xxxl.avatar-active:after {
  width: 32px;
  height: 32px;
}

.avatar-active.xxxl:after {
  width: 32px;
  height: 32px;
}
