.list {
  margin: 0px;
  padding: 0px;
  position: relative;
  display: flex;
  gap: 20px;
}

.list.column {
  flex-direction: column;
}

.list.row {
  flex-direction: row;
}

.list-item {
  display: flex;
  gap: 10px;
  list-style-type: none;
  font-family: 'Lexend', sans-serif;
}

.list-item .content {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.list-item .title {
  font-weight: 600;
  font-size: 16px;
  line-height: 24px;
}

.list-item .description {
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  color: var(--primary-neutral-500);
}

.list-item .image {
  width: 56px;
  height: 56px;
  border-radius: 12px;
  display: flex;
  justify-content: center;
  overflow: hidden;
  align-items: center;
}

.list-item svg {
  fill: var(--primary-neutral-900);
  width: 24px;
  height: 24px;
}
