.button {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;

  font-family: 'Lexend', sans-serif;
  border-radius: 8px;

  border: 0px;
  font-weight: 600;
  color: #fff;
  cursor: pointer;
  -webkit-appearance: button;

  transition-property: color, background-color, border-color, text-decoration-color, fill, stroke, opacity, box-shadow, transform, filter,
    backdrop-filter;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  transition-duration: 150ms;
}

.button.large {
  padding: 10px 20px;
  line-height: 28px;
  font-size: 18px;
  padding: 10px 20px;
  gap: 10px;
}

.button svg {
  height: 20px;
}

.button.medium {
  padding: 8px 16px;
  font-size: 16px;
  line-height: 24px;
  padding: 8px 16px;
  gap: 8px;
}

.button.small {
  padding: 6px 12px;
  font-size: 14px;
  line-height: 20px;
  padding: 6px 12px;
  gap: 8px;
}

.button.extrasmall {
  padding: 4px 12px;
  font-size: 14px;
  line-height: 20px;
  padding: 4px 12px;
  gap: 6px;
}

/* global */
.button:disabled {
  cursor: not-allowed;
}
/* deafult */
.button.default.filled {
  background-color: var(--primary-500);
  color: var(--white);
}
.button.default.filled:hover {
  background-color: var(--primary-700);
}
.button.default.filled:focus {
  outline: 4px solid var(--primary-100);
}
.button.default.filled:disabled {
  background-color: var(--primary-100);
}
.button.default.filled svg {
  fill: var(--white);
}

.button.default.accent {
  background-color: var(--primary-50);
  color: var(--primary-500);
}
.button.default.accent svg {
  fill: var(--primary-500);
}
.button.default.accent:hover {
  background-color: var(--primary-100);
}
.button.default.accent:focus {
  outline: 4px solid var(--primary-100);
}
.button.default.accent:disabled {
  color: var(--primary-200);
}
.button.default.accent:disabled svg {
  fill: var(--primary-200);
}

.button.default.outlined {
  background-color: var(--white);
  outline: 1px solid var(--primary-neutral-100);
  color: var(--primary-500);
}
.button.default.outlined svg {
  fill: var(--primary-500);
}
.button.default.outlined:hover {
  outline: 1px solid var(--primary-500);
}
.button.default.outlined:focus {
  outline: 4px solid var(--primary-100);
}
.button.default.outlined:disabled {
  color: var(--primary-200);
}
.button.default.outlined:disabled svg {
  fill: var(--primary-200);
}

.button.default.text {
  background-color: var(--white);
  color: var(--primary-500);
}
.button.default.text svg {
  fill: var(--primary-500);
}
.button.default.text:hover {
  color: var(--primary-700);
}
.button.default.text:hover svg {
  fill: var(--primary-700);
}

.button.default.text:focus {
  border-bottom: 2px solid var(--primary-100);
  border-radius: 0;
}
.button.default.text:disabled {
  color: var(--primary-200);
}
.button.default.text:disabled svg {
  fill: var(--primary-200);
}

