.step {
  margin: 15px;
  border-radius: 50%;
  border: 2px solid #ccc;
  width: 40px;
  height: 40px;
  position: relative;

  display: flex;
  align-items: center;
  justify-content: center;
}

.step.finished {
  color: var(--white);
  border-color: var(--primary-500);
  background-color: var(--primary-500);
}

.step.active {
  border-color: var(--primary-500);
  box-shadow: 0px 0px 0px 4px var(--primary-50);
}

.step.active .circle {
  border-radius: 50%;
  background-color: var(--primary-500);
  height: 16px;
  width: 16px;
}

.step.finished .circle {
  color: var(--white);
  background-color: var(--primary-500);
  /* height: 30px; */
  /* width: 30px; */
}
.step.finished .circle::after {
  font-size: 30px;
  content: '\2713';
}

.step .line-y {
  border-left: 2px solid #ccc;
  height: 16px;
  width: 1px;
  position: absolute;
}

.step .line-x {
  border-top: 2px solid #ccc;
  height: 1px;
  width: 16px;
  position: absolute;
}

.step .line-y.line-up {
  top: -17px;
  left: 19px;
}
.step .line-y.line-down {
  bottom: -17px;
  left: 19px;
}
.step.active .line-y.line-up {
  border-color: var(--primary-500);
}
.step.finished .line-y.line-up {
  border-color: var(--primary-500);
}
.step.finished .line-y.line-down {
  border-color: var(--primary-500);
}

.step .line-x.line-up {
  left: -17px;
  bottom: 18px;
}
.step .line-x.line-down {
  right: -17px;
  bottom: 18px;
}
.step.active .line-x.line-up {
  border-color: var(--primary-500);
}
.step.finished .line-x.line-up {
  border-color: var(--primary-500);
}
.step.finished .line-x.line-down {
  border-color: var(--primary-500);
}
