.carousel {
    padding-left: 0;
    list-style: none;

}

.carousel li {
    display: inline-block;

}

.carousel li:not(:last-child) {
    margin-right: 8px;
}

.carousel li {
    width: 10px;
    height: 10px;
    border-radius: 100%;
    background-color: #000;
    vertical-align: middle;
}

.carousel li:nth-child(1) {
    width: 4px;
    height: 4px;
    opacity: 0.32;
}

.carousel li:nth-child(2) {
    width: 6px;
    height: 6px;
    opacity: 0.32;
}

.carousel li:nth-child(4) {
    width: 6px;
    height: 6px;
    opacity: 0.32;
}

.carousel li:nth-child(5) {
    width: 6px;
    height: 6px;
    opacity: 0.32;
}

.carousel.small li:nth-child(2) {
    width: 10px;
    height: 10px;
    opacity: 1;
}

.carousel.small li:nth-child(1) {
    width: 6px;
    height: 6px;
    opacity: 0.32;
}

.carousel.small li:nth-child(3) {
    width: 6px;
    height: 6px;
    opacity: 0.32;
}