.tag {
  width: fit-content;
  border-radius: 16px;
  font-family: 'Lexend', sans-serif;

  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  color: #fff;
  border: 1px solid;
  box-shadow: none;
  line-height: 20px;
  font-weight: 400;
}

.tag svg {
  width: 14px;
  height: 14px;
  fill: var(--white);
}

.tag.medium {
  height: 28px;
  padding: 4px 16px;
  gap: 8px;
  font-size: 14px;
}

.tag.small {
  height: 24px;
  padding: 2px 8px;
  gap: 4px;
  font-size: 14px;
}

.tag.extrasmall {
  height: 20px;
  padding: 0px 8px;
  gap: 4px;
  font-size: 12px;
}

/* primary */
.tag.filled.primary {
  background-color: var(--primary-500);
}
.tag.accent.primary {
  color: var(--primary-500);
  background-color: var(--primary-50);
  border-color: var(--primary-50);
}
.tag.accent.primary svg {
  fill: var(--primary-500);
}
.tag.outline.primary {
  color: var(--primary-500);
  border-color: var(--primary-500);
}
.tag.outline.primary svg {
  fill: var(--primary-500);
}

