.breadcrumbs {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  padding: 0 0;
  margin-bottom: 1rem;

  list-style: none;
}

.breadcrumb-icon {
  height: 1rem;
  width: 1rem;
  fill: var(--primary-500);
  color: var(--primary-500);
}

.breadcrumb-icon svg {
  fill: var(--primary-500);
}

.breadcrumb-item {
  color: var(--primary-500);
  margin: 0;
  padding: 0;
  padding-right: 0.5rem;
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.breadcrumb-item:last-child {
  padding-right: 0;
}

.breadcrumb-item:hover {
  color: var(--primary-700);
  cursor: pointer;
}
.breadcrumb-item:hover svg {
  fill: var(--primary-700);
}

.breadcrumb-item.active {
  color: var(--primary-neutral-300);
}

.breadcrumb-item + .breadcrumb-item::before {
  float: left;
  color: var(--primary-neutral-300);
  content: '/';
}

.breadcrumb-dropdown .breadcrumb-item::before {
  content: '';
}

/* dropdown */
.breadcrumbs-dropdown {
  position: relative;
}
.breadcrumbs-dropdown:hover .breadcrumb-dropdown {
  display: block;
}

.breadcrumb-dropdown {
  top: 1rem;
  display: none;
  position: absolute;
  background-color: var(--white);
  border-radius: 6px;
  min-width: 160px;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  z-index: 1;
  padding: 1rem;
}

.breadcrumbs-dropdown:hover .breadcrumb-dropdown {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

/* active */
.breadcrumb-item.active {
  color: var(--primary-neutral-300);
}
.breadcrumb-item.active svg {
  fill: var(--primary-neutral-300);
}

/* disabled */
.breadcrumb-item.disabled {
  color: var(--primary-neutral-500);
}
.breadcrumb-item.disabled:hover {
  color: var(--primary-neutral-500);
  cursor: default;
}
.breadcrumb-item.disabled svg {
  fill: var(--primary-neutral-500);
}
.breadcrumbs-dropdown.disabled:hover .breadcrumb-dropdown {
  display: none;
}
