.alert {
  font-family: 'Lexend', sans-serif;
  position: relative;
  padding: 1rem 1rem;
  margin-bottom: 1rem;
  border-radius: 1rem;
  display: flex;
  gap: 10px;
  align-items: flex-start;
  font-weight: 600;
  font-size: 16px;
  line-height: 16px;
}

.alert-main {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 8px;
  order: 0;
  flex-grow: 1;
}

.alert-title {
  font-size: 16px;
  line-height: 24px;
}

.alert-description {
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
}

.alert-icon {
  width: 24px;
  height: 24px;
}

.alert-btns {
  margin-top: 16px;
  display: flex;
  gap: 20px;
}

.alert-btn {
  border: none;
  padding: 0;
  margin: 0;
  cursor: pointer;
  -webkit-appearance: button;
  background: none;
  font-weight: 600;
  color: inherit;
}
.alert-btn:hover {
  opacity: 0.6;
}
.alert-close-btn {
  width: 24px;
  height: 24px;
  order: 1;
  flex-grow: 0;
  align-items: center;
}

.alert.info {
  color: var(--blue-900);
  background-color: var(--blue-50);
}
.alert.info .alert-description {
  color: var(--blue-500);
}
.alert.info svg {
  fill: var(--blue-900);
}
.alert.info.dark {
  background-color: var(--blue-500);
}

.alert.success {
  color: var(--green-900);
  background-color: var(--green-50);
}
.alert.success .alert-description {
  color: var(--green-500);
}
.alert.success svg {
  fill: var(--green-900);
}
.alert.success.dark {
  background-color: var(--green-500);
}

.alert.warning {
  color: var(--orange-900);
  background-color: var(--orange-50);
}
.alert.warning .alert-description {
  color: var(--orange-500);
}
.alert.warning svg {
  fill: var(--orange-900);
}
.alert.warning.dark {
  background-color: var(--orange-500);
}

.alert.error {
  color: var(--red-900);
  background-color: var(--red-50);
}
.alert.error .alert-description {
  color: var(--red-500);
}
.alert.error svg {
  fill: var(--red-900);
}
.alert.error.dark {
  background-color: var(--red-500);
}

.alert.dark {
  color: var(--white);
}
.alert.dark svg {
  fill: var(--white);
}
.alert.dark .alert-description {
  color: var(--white);
}
