.tooltip {
  position: relative;
  display: inline-block;
}

.tooltip .tooltiptext {
  visibility: hidden;
  text-align: center;
  border-radius: 6px;
  padding-left: 16px;
  position: absolute;
  z-index: 1;
  top: -70px;
  left: 100%;
  /* Fade in tooltip - takes 1 second to go from 0% to 100% opac: */
  opacity: 0;
  transition: opacity 0.5s;
}

.tooltip:hover .tooltiptext {
  visibility: visible;
  opacity: 1;
}
.tooltiptext:hover {
  visibility: visible;
  opacity: 1;
}
.tooltiptext:hover:after {
  visibility: visible;
  opacity: 1;
}

.tooltipbox {
  display: flex;
  flex-direction: row;
  padding: 16px;
  width: 400px;
  box-shadow: 0 10px 15px -3px rgb(0 0 0 / 0.1), 0 4px 6px -4px rgb(0 0 0 / 0.1);
  gap: 10px;
  background: #ffffff;
  border-radius: 8px;
  position: relative;
}
.tooltipbox:after {
  width: 0;
  height: 0;
  border-right: 9px solid #fff;
  border-top: 9px solid transparent;
  border-bottom: 9px solid transparent;
  content: '';
  position: absolute;
  top: 45%;
  right: 100%;
  margin: 0px auto;
  box-shadow: 0 10px 15px -3px rgb(0 0 0 / 0.1), 0 4px 6px -4px rgb(0 0 0 / 0.1);
}
.tooltipbox .buttons {
  display: flex;
  justify-content: center;
  gap: 15px;
}
.tooltipbox .buttons a {
  border-radius: 8px;
  padding: 8px 16px;

  font-weight: 600;
  font-size: 16px;
  text-decoration: none;
}
.tooltipbox .buttons .btn-secondary {
  color: #6002ee;
  background: #efe5fd;
}
.tooltipbox .buttons .btn-primary {
  color: #fff;
  background: #6002ee;
}
