.fileupload {

    border: 1px dashed #90A4AE;
    position: relative;
    padding: 16px 8px;
    text-align: center;
    border-radius: 8px;
}

.fileupload input {
    position: absolute;
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%;
    outline: none;
    opacity: 0;
    cursor: pointer;
    left: 0px;
    top: 0px;
}

.fileupload h3 span {
    color: #6002EE;
}

.fileupload h3 small {
    color: #607D8B;
}

.fileupload-backcolor {
    background-color: #ECEFF1;
}