.modal-background {
  background: rgba(0, 0, 0, 0.5);
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 100;

  -webkit-transition: all 0.2s ease-in-out 0s;
  -moz-transition: all 0.2s ease-in-out 0s;
  -ms-transition: all 0.2s ease-in-out 0s;
  transition: all 0.2s ease-in-out 0s;
}

.modal {
  display: flex;
  gap: 10px;
  flex-direction: column;

  background: var(--white);
  border: 1px solid var(--primary-neutral-50);
  box-shadow: 0px 16px 32px rgba(38, 50, 56, 0.08);
  border-radius: 8px;
  padding: 30px;
  z-index: 9999;

  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);

  font-style: normal;

  -webkit-transition: all 0.2s ease-in-out 0s;
  -moz-transition: all 0.2s ease-in-out 0s;
  -ms-transition: all 0.2s ease-in-out 0s;
  transition: all 0.2s ease-in-out 0s;
}

.modal.vertical {
  width: 512px;
  text-align: left;
}
.modal.horizontal {
  width: 400px;
  text-align: center;
}

.modal.close {
  opacity: 0;
  visibility: hidden;
}
.modal.open {
  opacity: 1;
  visibility: visible;
}

.modal-icon {
  color: var(--primary-500);
}
.modal-icon svg {
  fill: var(--primary-500);
}
.modal.horizontal .modal-icon {
  margin: auto;
}

.modal-title {
  color: var(--primary-neutral-900);
  font-weight: 600;
  font-size: 18px;
  line-height: 28px;
}

.modal-description {
  color: var(--primary-neutral-500);
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
}

.modal-buttons {
  margin-top: 1.5rem;
}
.modal.vertical .modal-buttons {
  display: flex;
  flex-direction: row;
  justify-content: right;
  align-items: center;
  gap: 10px;
}

.modal.horizontal .modal-buttons {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 10px;
}

.modal.horizontal .modal-buttons button {
  width: 100%;
}

