.checkbox {
  display: flex;
  gap: 10px;
  flex-direction: row;
  cursor: pointer;
  align-items: center;
}
.checkbox.md {
  font-size: 16px;
}
.checkbox.sm {
  font-size: 14px;
}

input[type='checkbox'] {
  appearance: none;
  background-color: #fff;
  margin: 0;
  font: inherit;
  cursor: pointer;
  color: currentColor;
  border: 1px solid #cfd8dc;

  display: grid;
  place-content: center;
}

input[type='checkbox'].md {
  height: 25px;
  width: 25px;
}
input[type='checkbox'].sm {
  height: 16px;
  width: 16px;
}
input[type='checkbox'].square.md {
  border-radius: 6px;
}
input[type='checkbox'].square.sm {
  border-radius: 3px;
}
input[type='checkbox'].circle {
  border-radius: 100%;
}

input[type='checkbox']:hover {
  background-color: var(--primary-50);
  border-color: var(--primary-100);
}

input[type='checkbox']:focus {
  outline: 2px solid var(--primary-100);
}

input[type='checkbox']:checked {
  background-color: var(--primary-500);
  border-color: var(--primary-500);
}

input[type='checkbox']:checked:hover {
  background-color: var(--primary-700);
  border-color: var(--primary-700);
}

input[type='checkbox']:checked:focus {
  background-color: var(--primary-500);
  border-color: var(--primary-500);
}

input[type='checkbox']:disabled {
  background-color: var(--primary-neutral-100);
  border-color: var(--primary-neutral-100);
}

input[type='checkbox']::before {
  content: '';
  width: 0.65em;
  height: 0.65em;
  transform: scale(0);
  transition: 120ms transform ease-in-out;
  box-shadow: inset 1em 1em var(--white);
  clip-path: polygon(14% 44%, 0 65%, 50% 100%, 100% 16%, 80% 0%, 43% 62%);
}

input[type='checkbox']:checked::before {
  transform: scale(1);
}

input[type='checkbox'].minus::before {
  clip-path: polygon(100% 37%, 100% 58%, 0% 58%, 0% 37%);
}
