.button-group {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;

  font-family: 'Lexend', sans-serif;
  border-radius: 8px;
  border: 1px solid var(--primary-neutral-500);

  font-weight: 600;
  color: #fff;
  cursor: pointer;
  -webkit-appearance: button;

  background-color: var(--primary-neutral-900);

  transition-property: color, background-color, border-color, text-decoration-color, fill, stroke, opacity, box-shadow, transform, filter,
    backdrop-filter;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  transition-duration: 150ms;
}

.button-group svg {
  fill: var(--white);
  height: 16px;
}

.button-group.large {
  padding: 10px 20px;
  line-height: 28px;
  font-size: 18px;
  padding: 10px 20px;
  gap: 10px;
}

.button-group.medium {
  padding: 8px 16px;
  font-size: 16px;
  line-height: 24px;
  padding: 8px 16px;
  gap: 8px;
}

.button-group.small {
  padding: 6px 12px;
  font-size: 14px;
  line-height: 20px;
  padding: 6px 12px;
  gap: 8px;
}

.button-group.extrasmall {
  padding: 4px 12px;
  font-size: 14px;
  line-height: 20px;
  padding: 4px 12px;
  gap: 6px;
}

.button-group.large {
  padding: 10px 20px;
  line-height: 28px;
  font-size: 18px;
  padding: 10px 20px;
  gap: 10px;
}

.button-group.medium {
  padding: 8px 16px;
  font-size: 16px;
  line-height: 24px;
  padding: 8px 16px;
  gap: 8px;
}

.button-group.small {
  padding: 6px 12px;
  font-size: 14px;
  line-height: 20px;
  padding: 6px 12px;
  gap: 8px;
}

.button-group.left {
  border-radius: 8px 0px 0px 8px;
}

.button-group.right {
  border-radius: 0px 8px 8px 0px;
}

.button-group.center {
  border-radius: 0px;
}

.button-group:hover {
  background-color: var(--primary-neutral-800);
}

.button-group:focus {
  background-color: var(--primary-neutral-700);
}

.button-group:disabled {
  background-color: var(--primary-neutral-100);
  border-color: var(--primary-neutral-200);
  cursor: not-allowed;
}
