.progressbar {
  width: 114px;
  height: 114px;
  position: relative;
}
.progressbar .num {
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  color: #111;
}
.progressbar svg {
  width: 114px;
  height: 114px;
  position: relative;
}
.bg {
  fill: none;
  stroke-width: 10px;
  stroke: #efe5fd;
}
[class^='meter-'] {
  fill: none;
  stroke-width: 10px;
  stroke-linecap: round;
  transform: rotate(-90deg);
  transform-origin: 50% 50%;
}
.meter-1 {
  stroke-dasharray: 360;
  stroke-dashoffset: 180;
  stroke: #6002ee;
  animation: progress-1 1s ease-out;
}

.progressbar span.text {
  text-align: center;
  display: block;
}
.progressbar.static {
  width: 80%;
  background: #efe5fd;
  border-radius: 8px;
  height: 8px;
  position: relative;
}
.progressbar.static .color_line {
  width: 80%;
  background: #6002ee;
  border-radius: 8px;
  height: 8px;
  animation: progress 1500ms ease-in 1;
}
.progressbar.static .percentage {
  position: absolute;
  right: -42px;
  top: -8px;
}

@keyframes progress {
  from {
    width: 0;
  }
}
