.dropdown {
  position: relative;
}

.dropdown-button {
  display: flex;
  flex-direction: row;
  align-items: center;
  padding-left: 16px;
  width: 284px;
  height: 46px;
  border: 1px solid var(--primary-neutral-100);
  border-radius: 8px;

  text-align: left;
  cursor: pointer;

  transition-property: color, background-color, border-color, text-decoration-color, fill, stroke, opacity, box-shadow, transform, filter,
    backdrop-filter;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  transition-duration: 150ms;
}

.dropdown-button:hover {
  background-color: var(--primary-neutral-100);
}

.dropdown-box {
  top: 48px;
  left: 0;

  display: flex;
  gap: 8px;
  padding-top: 8px;
  padding-bottom: 8px;
  flex-direction: column;
  position: absolute;
  background-color: var(--white);
  border: 1px solid var(--primary-neutral-100);
  border-radius: 8px;
  width: 300px;
  z-index: 1;

  -webkit-transition: all 0.2s ease-in-out 0s;
  -moz-transition: all 0.2s ease-in-out 0s;
  -ms-transition: all 0.2s ease-in-out 0s;
  transition: all 0.2s ease-in-out 0s;
}

.dropdown-box.close {
  /* display: none; */
  opacity: 0;
  visibility: hidden;
}
.dropdown-box.open {
  opacity: 1;
  visibility: visible;
}

.dropdown-item {
  height: 40px;
  display: flex;
  align-items: center;
  padding-left: 16px;
  cursor: pointer;
}
.dropdown-item:hover {
  background-color: var(--primary-neutral-100);
}
